import { Box, Container, Link, Tooltip } from "@mui/material";
import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faReact,
  faGithub,
  faTwitter,
  faLinkedin,
} from "@fortawesome/free-brands-svg-icons";

const Footer = () => {
  return (
    <Container
      maxWidth="md"
      sx={{ boxShadow: 1, borderRadius: 2, p: 2, minWidth: 300 }}
    >
      <Box
        sx={{
          textAlign: "center",
        }}
      >
        <p>
          &copy; {new Date().getFullYear()} <span>Portafolio</span> Construido
          con{" "}
          <Tooltip title="React" arrow>
            <Link
              target="_blank"
              rel="noreferrer"
              href="https://es.reactjs.org/"
            >
              <FontAwesomeIcon icon={faReact} className="fa-lg" />
            </Link>
          </Tooltip>
        </p>
        <Box sx={{ color: "lightgray" }}>
          <Link
            target="_blank"
            rel="noreferrer"
            href="https://github.com/javicoro99"
          >
            <FontAwesomeIcon icon={faGithub} className="fa-lg" />
          </Link>{" "}
          |{" "}
          <Link
            target="_blank"
            rel="noreferrer"
            href="https://twitter.com/javicoro99"
          >
            <FontAwesomeIcon icon={faTwitter} className="fa-lg" />
          </Link>{" "}
          |{" "}
          <Link
            target="_blank"
            rel="noreferrer"
            href="https://www.linkedin.com/in/javier-darwin-coronado-velasquez/"
          >
            <FontAwesomeIcon icon={faLinkedin} className="fa-lg" />
          </Link>
        </Box>
      </Box>
    </Container>
  );
};

export default Footer;
