import { Container, Grid, Typography } from "@mui/material";
import React from "react";
import { data } from "../assets/data/data";
import ContacForm from "../Components/ContacForm";
const Contactame = () => {
  return (
    <Container
      maxWidth="md"
      sx={{
        borderRadius: 0,
        p: 2,
        minWidth: 300,
      }}
    >
      <Grid container spacing={2} columns={16}>
        <Grid item md={8}>
          <Typography variant="h5" fontFamily={"Fredoka"} mb={2} color="blue">
            {data.paginaDe.Contactame.titulo}
          </Typography>
          <Typography variant="body1">
            {data.paginaDe.Contactame.descripcion}
          </Typography>
        </Grid>
        <Grid item md={8}>
          <ContacForm />
        </Grid>
      </Grid>
    </Container>
  );
};

export default Contactame;
