import React from "react";
import { Container, Typography, Box, Button, Paper } from "@mui/material";
import { data } from "../assets/data/data";
import { Link } from "react-router-dom";

const PaperStyle = {
  maxWidth: 500,
  m: 3,
  "@media (max-width:500px)": {
    maxWidth: 300,
    m: 1,
  },
};

const Resumen = () => {
  return (
    <Container
      maxWidth="md"
      sx={{
        borderRadius: 0,
        p: 2,
        minWidth: 300,
      }}
    >
      <Typography
        variant="h3"
        textAlign={"center"}
        sx={{
          textDecoration: "underline",
          textDecorationColor: "lightblue",
        }}
      >
        {data.paginaDe.resumen.titulo}
      </Typography>
      <Typography variant="h4" fontFamily={"Fredoka"} textAlign={"center"}>
        {data.paginaDe.resumen.encabezado}
      </Typography>
      <Box sx={{ mt: "20px" }}>
        {data.paginaDe.resumen.cuentas.map((a, i) => (
          <Box key={i}>
            <Typography variant="subtitle2" color={"blue"}>
              {a[0]}
            </Typography>
            <Typography>{a[1]}</Typography>
          </Box>
        ))}
      </Box>
      <Typography variant="h5" fontFamily={"Fredoka"} sx={{ mt: "20px" }}>
        {data.paginaDe.resumen.seccion.primera.titulo}
      </Typography>
      <Box sx={{ mt: "10px" }}>
        {data.paginaDe.resumen.seccion.primera.conocimientos.map((a, i) => (
          <Box key={i}>
            <Typography variant="subtitle2" color={"darkorange"}>
              {a[0]}
            </Typography>
            <Typography>{a[1]}</Typography>
          </Box>
        ))}
      </Box>
      <Typography variant="h5" fontFamily={"Fredoka"} sx={{ mt: "20px" }}>
        {data.paginaDe.resumen.seccion.segunda.titulo}
      </Typography>
      <Container
        maxWidth="md"
        sx={{
          borderRadius: 0,
          p: 2,
          minWidth: 300,
          display: "flex",
          flexFlow: "column",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <Paper
          component={"img"}
          src="/images/Toquecito.png"
          sx={PaperStyle}
        ></Paper>
        <Paper
          component={"img"}
          src="/images/Calculado.png"
          sx={PaperStyle}
        ></Paper>
        <Paper
          component={"img"}
          src="/images/Pomodo.png"
          sx={PaperStyle}
        ></Paper>
      </Container>
      <Button variant="contained" component={Link} to="/Proyectos">
        {data.paginaDe.resumen.seccion.segunda.mensaje}
      </Button>
      <Typography variant="h5" fontFamily={"Fredoka"} sx={{ mt: "20px" }}>
        {data.paginaDe.resumen.seccion.tercero.titulo}
      </Typography>
      <Box sx={{ mt: "10px" }}>
        {data.paginaDe.resumen.seccion.tercero.pasatiempos.map((a, i) => (
          <Box key={i}>
            <Typography variant="subtitle2" color={"blue"}>
              {a[0]}{" "}
              <Typography
                sx={{
                  mr: "50%",
                  "@media (max-width:800px)": {
                    mr: "0",
                  },
                }}
                color={"black"}
              >
                {" "}
                {a[1]}
              </Typography>
            </Typography>
          </Box>
        ))}
      </Box>
    </Container>
  );
};

export default Resumen;
