import { styled } from "@mui/material/styles";
import { ButtonBase, Container, Grid, Paper, Typography } from "@mui/material";
import React from "react";
import SEO from "../Components/SEO";
import { data } from "../assets/data/data";
import { Link } from "react-router-dom";

const Img = styled("img")({
  margin: "auto",
  display: "block",
  maxWidth: "100%",
  maxHeight: "100%",
});

const Home = () => {
  return (
    <Container
      maxWidth="md"
      sx={{
        boxShadow: 1,
        borderRadius: 0,
        p: 2,
        minWidth: 300,
      }}
    >
      <SEO title={"Home"} />
      {/* Primer Recuadro */}
      <Paper
        sx={{
          p: 2,
          margin: "2% auto 2% 0 ",
          maxWidth: 400,
          flexGrow: 1,
        }}
        elevation={3}
      >
        <Grid container spacing={2}>
          <Grid item xs={4} sm={4} md={4}>
            <ButtonBase component={Link} to="/Resumen">
              <Img alt="complex" src={data.miniatura.resumen.url} />
            </ButtonBase>
          </Grid>
          <Grid item xs={6} container>
            <Grid item xs container direction="column" spacing={2}>
              <Grid item xs>
                <Typography
                  gutterBottom
                  variant="h5"
                  component="div"
                  fontFamily={"Fredoka"}
                >
                  {data.miniatura.resumen.titulo}
                </Typography>
                <Typography variant="body2" textAlign={"justify"}>
                  {data.miniatura.resumen.descripcion}
                </Typography>
                <Typography
                  variant="body2"
                  color="text.secondary"
                  component={Link}
                  to="/Resumen"
                >
                  {" "}
                  ver mas...
                </Typography>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Paper>
      {/* Segundo Recuadro */}
      <Paper
        sx={{
          p: 2,
          margin: "2% 0 2% auto",
          maxWidth: 400,
          flexGrow: 1,
          "@media (max-width:)": {},
        }}
        elevation={3}
      >
        <Grid container spacing={2}>
          <Grid item xs={8} container>
            <Grid item xs container direction="column" spacing={2}>
              <Grid
                item
                xs
                // sx={{
                //   direction: "rtl",
                // }}
                textAlign={"right"}
              >
                <Typography
                  gutterBottom
                  variant="h5"
                  component="div"
                  fontFamily={"Fredoka"}
                >
                  {data.miniatura.proyectos.titulo}
                </Typography>
                <Typography variant="body2">
                  {data.miniatura.proyectos.descripcion}
                </Typography>
                <Typography
                  variant="body2"
                  color="text.secondary"
                  component={Link}
                  to="/Proyectos"
                >
                  {" "}
                  ver mas...
                </Typography>
              </Grid>
            </Grid>
          </Grid>
          <Grid item xs={4} sm={4} md={4}>
            <ButtonBase component={Link} to="/Proyectos">
              <Img alt="complex" src={data.miniatura.proyectos.url} />
            </ButtonBase>
          </Grid>
        </Grid>
      </Paper>
      {/* Ultimo recuadro */}
      <Paper
        sx={{
          p: 2,
          margin: "2% auto 2% 0 ",
          maxWidth: 400,
          flexGrow: 1,
        }}
        elevation={3}
      >
        <Grid container spacing={2}>
          <Grid item xs={4} sm={4} md={4}>
            <ButtonBase component={Link} to={`${`/Dise%C3%B1os`}`}>
              <Img alt="complex" src={data.miniatura.diseos.url} />
            </ButtonBase>
          </Grid>
          <Grid item xs={6} container>
            <Grid item xs container direction="column" spacing={2}>
              <Grid item xs>
                <Typography
                  gutterBottom
                  variant="h5"
                  component="div"
                  fontFamily={"Fredoka"}
                >
                  {data.miniatura.diseos.titulo}
                </Typography>
                <Typography variant="body2" textAlign={"justify"}>
                  {data.miniatura.diseos.descripcion}
                </Typography>
                <Typography
                  variant="body2"
                  color="text.secondary"
                  component={Link}
                  to={`${`/Dise%C3%B1os`}`}
                >
                  {" "}
                  ver mas...
                </Typography>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Paper>
    </Container>
  );
};

export default Home;
