import React from "react";
import {
  Card,
  CardActions,
  CardContent,
  CardMedia,
  Button,
  Typography,
} from "@mui/material";

const Tarjeta = ({ img, titulo, descripcin, tecnologias, url, action }) => {
  return (
    <Card
      sx={{
        maxWidth: 250,
        "@media (max-width: 800px)": {
          maxWidth: "500px",
          display: "flex",
          alignItems: "center",
          flexFlow: "column",
        },
      }}
    >
      <CardMedia
        sx={{
          "@media (max-width: 800px)": {
            maxWidth: "250px",
          },
        }}
        component={"img"}
        image={img}
        alt="Toquecito Peruano"
      />
      <CardContent>
        <hr />
        <Typography variant="h4" textAlign={"center"}>
          {titulo}
        </Typography>
        <Typography variant="body1">{descripcin}</Typography>
        <Typography variant="subtitle1" color={"blue"}>
          {tecnologias.titulo}
        </Typography>
        <Typography variant="subtitle2">{tecnologias.cod}</Typography>
      </CardContent>
      <CardActions
        sx={{ display: "flex", justifyContent: "center", mb: "10px" }}
      >
        <Button href={url} target="_blank" variant="contained">
          {action}
        </Button>
      </CardActions>
    </Card>
  );
};

export default Tarjeta;
