import Typography from "@mui/material/Typography";
import { Box } from "@mui/system";
import { Link } from "react-router-dom";
import React from "react";
import { Button, Container } from "@mui/material";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEnvelope } from "@fortawesome/free-regular-svg-icons";

const Header = () => {
  return (
    <Container
      maxWidth="md"
      sx={{
        boxShadow: 1,
        borderRadius: 1,
        p: 2,
        minWidth: 300,
      }}
    >
      <Box
        component={"ul"}
        sx={{
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
          padding: ["0"],
          marginLeft: "5%",
          marginRight: "5%",
        }}
      >
        <Box>
          <Link to="/" style={styleLink}>
            <Typography
              variant="h3"
              sx={{
                fontFamily: "Fredoka",
                fontSize: "1.5rem",
                "@media (min-width:800px)": {
                  fontSize: "1.7rem",
                },
                color: "orange",
                cursor: "pointer",
              }}
            >
              Javier Coronado
            </Typography>
          </Link>
        </Box>
        <Box>
          {/* <Link
            to="/"
            style={{
              textDecoration: "none",
              color: "black",
              padding: "10px",
            }}
          >
            Home
          </Link> */}
          {/* <Link to="/Resumen" style={styleLink}>
            Resumen
          </Link> */}
          {/* <Link to={`${`/Dise%C3%B1os`}`} style={styleLink}>
            Diseos
          </Link> */}
          <Link to="/Contactame" style={styleLink}>
            <Button
              variant="contained"
              endIcon={<FontAwesomeIcon icon={faEnvelope} />}
            >
              Contactame
            </Button>
          </Link>
          {/* <Link to="/Proyectos" style={styleLink}>
            Proyectos
          </Link> */}
        </Box>
      </Box>
    </Container>
  );
};

export default Header;

const styleLink = {
  textDecoration: "none",
  color: "black",
};
