import { styled } from "@mui/material/styles";
import {
  ButtonBase,
  Container,
  Grid,
  Paper,
  Typography,
  Link,
} from "@mui/material";
import React from "react";
import SEO from "../Components/SEO";
import { data } from "../assets/data/data";
const Img = styled("img")({
  margin: "auto",
  display: "block",
  maxWidth: "100%",
  maxHeight: "100%",
});

const Diseos = () => {
  return (
    <Container
      maxWidth="md"
      sx={{
        borderRadius: 0,
        p: 2,
        minWidth: 300,
      }}
    >
      <Typography
        variant="h3"
        textAlign={"center"}
        sx={{
          textDecoration: "underline",
          textDecorationColor: "lightblue",
        }}
      >
        {data.paginaDe.Diseos.titulo}
      </Typography>
      <SEO title={"Diseos"} />
      {/* Primer Recuadro */}
      <Paper
        sx={{
          p: 2,
          margin: "2% auto 2% 0 ",
          maxWidth: 400,
          flexGrow: 1,
          backgroundColor: "white",
        }}
        elevation={3}
      >
        <Grid container spacing={2}>
          <Grid item xs={4} sm={4} md={4} sx={{ display: "flex" }}>
            <ButtonBase
              component={Link}
              target="_blank"
              href={data.paginaDe.Diseos.j99.url}
            >
              <Img alt="complex" src={data.paginaDe.Diseos.j99.img} />
            </ButtonBase>
          </Grid>
          <Grid item xs={6} container>
            <Grid item xs container direction="column" spacing={2}>
              <Grid item xs>
                <Typography
                  gutterBottom
                  variant="h5"
                  component="div"
                  fontFamily={"Fredoka"}
                >
                  {data.paginaDe.Diseos.j99.titulo}
                </Typography>
                <Typography variant="body2">
                  {data.paginaDe.Diseos.j99.descripcion}
                </Typography>
                <Link
                  sx={{
                    color: "orange",
                    cursor: "pointer",
                  }}
                  underline="hover"
                  target="_blank"
                  href={data.paginaDe.Diseos.j99.url}
                >
                  {" "}
                  ver mas...
                </Link>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Paper>
      {/* Segundo Recuadro */}
      <Paper
        sx={{
          p: 2,
          margin: "2% 0 2% auto",
          maxWidth: 400,
          flexGrow: 1,
          "@media (max-width:)": {},
        }}
        elevation={3}
      >
        <Grid container spacing={2}>
          <Grid item xs={8} container>
            <Grid item xs container direction="column" spacing={2}>
              <Grid
                item
                xs
                // sx={{
                //   direction: "rtl",
                // }}
                textAlign={"right"}
              >
                <Typography
                  gutterBottom
                  variant="h5"
                  component="div"
                  fontFamily={"Fredoka"}
                >
                  {data.paginaDe.Diseos.alkapone.titulo}
                </Typography>
                <Typography variant="body2">
                  {data.paginaDe.Diseos.alkapone.descripcion}
                </Typography>
                <Link
                  sx={{
                    color: "orange",
                    cursor: "pointer",
                  }}
                  underline="hover"
                  target="_blank"
                  href={data.paginaDe.Diseos.j99.url}
                >
                  {" "}
                  ver mas...
                </Link>
              </Grid>
            </Grid>
          </Grid>
          <Grid item xs={4} sm={4} md={4} sx={{ display: "flex" }}>
            <ButtonBase
              component={Link}
              target="_blank"
              href={data.paginaDe.Diseos.alkapone.url}
            >
              <Img alt="complex" src={data.paginaDe.Diseos.alkapone.img} />
            </ButtonBase>
          </Grid>
        </Grid>
      </Paper>
      {/* Ultimo recuadro */}
      <Paper
        sx={{
          p: 2,
          margin: "2% auto 2% 0 ",
          maxWidth: 400,
          flexGrow: 1,
          backgroundColor: "white",
        }}
        elevation={3}
      >
        <Grid container spacing={2}>
          <Grid item xs={4} sm={4} md={4} sx={{ display: "flex" }}>
            <ButtonBase
              component={Link}
              target="_blank"
              href={data.paginaDe.Diseos.recetarioDeJordan.url}
            >
              <Img
                alt="complex"
                src={data.paginaDe.Diseos.recetarioDeJordan.img}
              />
            </ButtonBase>
          </Grid>
          <Grid item xs={6} container>
            <Grid item xs container direction="column" spacing={2}>
              <Grid item xs>
                <Typography
                  gutterBottom
                  variant="h5"
                  component="div"
                  fontFamily={"Fredoka"}
                >
                  {data.paginaDe.Diseos.recetarioDeJordan.titulo}
                </Typography>
                <Typography variant="body2">
                  {data.paginaDe.Diseos.recetarioDeJordan.descripcion}
                </Typography>
                <Link
                  sx={{
                    color: "orange",
                    cursor: "pointer",
                  }}
                  underline="hover"
                  target="_blank"
                  href={data.paginaDe.Diseos.recetarioDeJordan.url}
                >
                  {" "}
                  ver mas...
                </Link>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Paper>
    </Container>
  );
};

export default Diseos;
