import React from "react";
import { Home, Resumen, Diseos, Proyectos, Contactame } from "./Pages";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Layout from "./Components/Layout";

const App = () => {
  return (
    <Router>
      <Layout>
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/Resumen" element={<Resumen />} />
          <Route path={`${`/Dise%C3%B1os`}`} element={<Diseos />} />
          {/* Para usar la  se tiene que matchear con los simbolos escritos arriba que es el valor para rutas de  c3 b1 pero en rutas seria %C3%B1, solo afecta a rutas en otro caso la  funciona con normalidad */}
          <Route path="/Contactame" element={<Contactame />} />
          <Route path="/Proyectos" element={<Proyectos />} />
        </Routes>
      </Layout>
    </Router>
  );
};

export default App;
