import React from "react";
import Header from "./Header";
import Footer from "./Footer";
import { Container } from "@mui/material";
import "../assets/css/Fonts.css";
import "normalize.css";

const Layout = ({ children }) => {
  return (
    <Container
      sx={{
        fontFamily: "roboto",
      }}
    >
      <Header />
      {children}
      <Footer />
    </Container>
  );
};

export default Layout;
