import React from "react";
import SEO from "../Components/SEO";
import { Container, Grid, Typography } from "@mui/material";
import { data } from "../assets/data/data";
import Tarjeta from "../Components/Tarjeta";

const Proyectos = () => {
  return (
    <Container
      maxWidth="md"
      sx={{
        borderRadius: 0,
        p: 2,
        minWidth: 300,
      }}
    >
      <Typography
        variant="h3"
        textAlign={"center"}
        sx={{
          textDecoration: "underline",
          textDecorationColor: "lightblue",
        }}
        mb={"20px"}
      >
        {data.paginaDe.Proyectos.titulo}
      </Typography>
      <SEO title={"Home"} />
      <Grid container spacing={8} columns={15}>
        {data.paginaDe.Proyectos.indice.map((a, i) => (
          <Grid item key={i} md={5} sm={7.5} xs={18}>
            <Tarjeta
              key={i}
              img={a.img}
              titulo={a.titulo}
              descripcin={a.descripcion}
              tecnologias={a.tecnologias}
              url={a.url}
              action={a.action}
            />
          </Grid>
        ))}
      </Grid>
    </Container>
  );
};

export default Proyectos;
