import React from "react";
import { TextField, Button, Box, Container } from "@mui/material";

const ContacForm = () => {
  return (
    <Container
      component={"form"}
      className="form contact-form"
      action="https://formspree.io/f/xgedzqna"
      method="POST"
    >
      <Box mb={1}>
        <TextField
          id="name"
          label="Tu nombre"
          variant="outlined"
          name="nombre"
          sx={{
            mb: 1,
            width: "40ch",
          }}
        />

        <TextField
          id="email"
          label="Tu email"
          variant="outlined"
          name="email"
          sx={{
            mb: 1,
            width: "40ch",
          }}
        />

        <TextField
          id="outlined-multiline-flexible"
          label="Mensaje"
          variant="outlined"
          name="mensaje"
          multiline
          maxRows={8}
          sx={{
            mb: 1,
            width: "40ch",
          }}
        />
      </Box>
      <Button type="submit" variant="contained">
        Enviar
      </Button>
    </Container>
  );
};

export default ContacForm;
